@import "variables";
@import "mixins";
.fa-spinner {
  font-size: 120px;
  color: #203375;
}
.fa-plane {
  &.fa-90 {
    transform: rotate(90deg);
  }
  &.fa-45 {
    transform: rotate(45deg);
  }
  &.fa-225 {
    transform: rotate(225deg);
  }
}
.flights-listing-wrapper {
  .summary-wrapper {
    padding: 0 15px;
    margin-bottom: 30px;
    .col-sm {
      padding-top: 10px;
      padding-bottom: 10px;
      border: 1px solid $golden;
      border-right-width: 0;
      @media screen and (max-width: $screen-xs) {
        border-right-width: 1px;
        margin-bottom: -1px;
        margin-right: -1px;
      }
      &:nth-of-type(1) {
        min-width: 250px;
      }
      &:nth-of-type(3) {
        max-width: 100px;
      }
      &:last-of-type {
        border-right-width: 1px;
      }
    }
  }
  .flight-list-items {
    border: 1px solid red;
  }
  .flight-list-item {
    border-radius: 5px;
    box-shadow: 0 9px 23px rgba(0, 0, 0, .09), 0 5px 5px rgba(0, 0, 0, .06);
    margin-bottom: 30px;
    .legs-row {
      padding-top: 15px;
      padding-bottom: 15px;
    }
    .itinerary-summary {
      padding: 15px 15px;
      color: $darkBlue;
      .stoppage-column {
        text-align: center;
        .info {
          margin-bottom: 0;
        }
      }
      .info > .info {
        display: inline-block;
      }
      @media screen and (max-width: $screen-xs) {
        margin: 15px 0;
      }
      > .row {
        margin-bottom: 30px;
        &:last-of-type {
          margin-bottom: 0;
        }
      }
    }
    .flight-detail-wrapper {
      padding-top: 6px;
      padding-bottom: 6px;
      border-top: 1px solid $silver;
      border-bottom: 1px solid $silver;
      .first-column {
        @media screen and (max-width: $screen-xs) {
          padding-right: 0;
        }
      }
      .second-column {
        padding-right: 5px;
        padding-left: 5px;
      }
      .third-column {
        padding-left: 0;
      }
      a[data-toggle="collapse"] {
        .fa {
          transition: 0.3s;
        }
        &[aria-expanded="true"] {
          .fa {
            transform: rotate(180deg);
          }
        }
      }
      .collapsing,
      .collapse.show {
        padding-top: 6px;
        margin-bottom: -6px;
      }
      .card {
        border: none;
        border-radius: 0 0 5px 5px;
        background-color: inherit;
        border-top: 1px solid $silver;
        .leg-details {
          padding: 0;
          .seg-details {
            margin-bottom: 15px;
            &:last-of-type {
              margin-bottom: 0;
              .lay-over {
                display: none;
              }
            }
            .column-c {
              .fa-plane {
                font-size: 32px;
              }
            }
          }
        }
      }
      .lay-over {
        margin-top: 15px;
        padding-top: 6px;
        padding-bottom: 6px;
        border-top: 1px dashed $black;
        border-bottom: 1px dashed $black;
      }
      .collapse.show + .collapse.show {
        .card {
          padding-top: 0;
          border-top: none;
        }
      }

      .flight-information {
        .leg-details {
          margin-bottom: 30px;
          &:last-of-type {
            margin-bottom: 0;
          }
          .seg-details {
            @media screen and (max-width: $screen-xs) {
              .col-sm.col-12 {
                margin-bottom: 10px;
                &:last-of-type {
                  margin-bottom: 0;
                }
              }
            }
            @media screen and (min-width: $screen-sm) {
              .col {
                min-width: 220px;
                max-width: 220px;
                &.column-e,
                &.column-c {
                  min-width: 100px;
                  max-width: 100px;
                }
              }
            }
          }
        }
      }
    }
    .stoppage {
      width: 100%;
      height: 1px;
      position: relative;
      background: $grey999;
      padding: 0 10px;
      max-width: 100px;
      margin: 10px auto !important;
      @media screen and (max-width: $screen-xs) {
        max-width: 200px;
      }
      &:before {
        position: absolute;
        content: "";
        width: 10px;
        height: 10px;
        border-radius: 50%;
        background: $grey999;
        left: 50%;
        margin-left: -5px;
        top: -5px;
      }
      &.none:before {
        display: none;
      }
    }
    .book-now {
      display: block;
      width: 100%;
      margin-top: 5px;
      border-radius: 0;
      box-shadow: -3px 3px 2px #666;
    }

    .price-table {
      thead,
      tfoot {
        background: $darkBlue;
        color: $white;
      }
      thead,
      tbody,
      tfoot {
        tr {
          td,
          th {
            &:nth-last-of-type(1) {
              text-align: right;
            }
          }
        }
      }
      tfoot {
        tr td {
          font-weight: 700;
        }
      }
    }
  }
  .info {
    font-size: 14px;
    line-height: 1.25;
    font-weight: 400;
    display: block;
    margin-bottom: 5px;
    &.no-wrap {
      white-space: nowrap;
    }
    &.large {
      font-size: 16px;
    }
    &.bold {
      font-weight: 700;
    }
    &.medium {
      font-weight: 600;
    }
    &:last-of-type {
      margin-bottom: 0;
    }
    .goldy,
    &.goldy {
      color: $golden;
    }
    &.separator {
      margin-left: 3px;
      margin-right: 3px;
    }
    &.price {
      font-size: 24px;
      font-weight: 700;
      text-align: center;
      color: $darkBlue;
    }
  }
  .todo {
    color: red !important;
  }
  &.test {
    .raw-data {
      display: block !important;
    }
  }
}


.filter-box {
  @media screen and (max-width: $screen-xs) {
    margin-bottom: 30px;
  }
  .title {
    display: block;
    background-color: $darkBlue;
    color: $white;
    padding: 10px 15px;
    font-size: 24px;
    a {
      display: block;
      width: 100%;
      position: relative;
      padding-right: 40px;
      .fa-chevron-down {
        width: 25px;
        height: 25px;
        color: $white;
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: auto;
        display: block;
        margin: auto;
        transition: 0.3s;
      }
      &[aria-expanded="true"] {
        .fa-chevron-down {
          transform: rotate(180deg);
        }
      }
      @media screen and (min-width: $screen-sm) {
        pointer-events: none;
        .fa-chevron-down {
          display: none;
        }
      }
    }
  }
  .collapse-filters {
    @media screen and (min-width: $screen-sm) {
      display: block !important;
    }
  }
  .filter-set {
    padding: 15px 15px;
    .filter-label {
      color: $darkBlue;
      font-size: 20px;
      display: block;
      padding-bottom: 5px;
      border-bottom: 1px solid $darkBlue;
      margin-bottom: 10px;
    }
    .form-group {
      &:last-of-type {
        margin-bottom: 0;
      }
    }
  }
}
